@font-face {
    font-family: myFont;
    src: url(/fonts/3979.ttf);
}

* {
    padding: 0;
    margin: 0;
    border: 1px solid black;
}

.header {
    position: relative;
    width: 1260px;
    height: 106px;
    margin: 0 auto;
    margin-top: 5px;
}

.logo {
    float: left;
    width: 88px;
    height: 106px;
    background-image: url(/img/rabbit1.jpg);
}

.span1 {
    float: left;
    width: 200px;
}

.orange {
    position: relative;
    font-size: 35px;
    top: 0px;
    color: orange;
    left: 60px;
}

.rabbit {
    display: block;
    font-size: 35px;
    position: relative;
    left: 57px;
    font-family: myFont;
    color: rgb(71, 71, 71)
}

.top-menu {
    float: left;
    margin-left: 451px;
}

.home, .about, .work, .blog, .contact {
    position: relative;
    top: 10px;
    float: left;
    font-size: 20px;
    height: 47px;
    width: 55px;
    text-align: center;
    margin: 0 23px;
}

a {
    display: block;
    text-decoration: none;
    color: grey;
}
/*end_header*/

.slider {
    height: 410px;
    width: 1620;
    background-image: url(/img/slider.jpg);
}

.span2 {
    width: 410px;
    position: relative;
    top: 150px;
    left: 50px;
    font-family: myFont;
    font-size: 35px;
    font-weight: bold;
}

.span3 {
    position: relative;
    color: orange;
    left: 5px;
    padding: 15px;
    padding-left: 30px;
    background-color: rgb(71, 71, 71);
}

.span4 {
    position: relative;
    color: rgb(71, 71, 71);
    background-color: rgb(233, 162, 30); 
    top: 55px;
    padding: 18px;
    padding-left: 30px;
}
/*end_slider*/

.main {
    margin: 0 auto;
    width: 1260px;
}

.content1 {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap; 
    justify-content: space-evenly; 
    font-size: 15px;
    font-weight: bold; 
}

.item1, .item2, .item3, .item4 {
    padding-left: 23px;
    width: 23%;
}

.item5, .item6, .item7, .item8 {
    text-align: right;
    color: orange;
    padding-right: 23px;
    width: 23%;
} 
/*end content1*/

.content2 {
    margin-top: 10px; 
}

.span5 {
    text-align: center;
    font-size: 30px;
    text-decoration: underline;
    color: orange;
}

.card {
    display: flex;
    margin-top: 10px;
    justify-content: space-around;
}